import {ADD_TO_CART, REMOVE_FROM_CART, CLEAR_PAY_CART} from "./types.js";

//this func is action creator
export function addToCart(productInfo, quantity){
  return {
    type: ADD_TO_CART,
    productInfo,
    quantity
  }
}

export function removeFromCart(index){
  return {
    type: REMOVE_FROM_CART,
    index
  }
}

export function clearPayCart(){
  return {
    type: CLEAR_PAY_CART,
  }
}