import React from "react";
import { connect } from "react-redux";
import { removeFromCart } from "../Store/actions/actions";
import { clearPayCart } from "../Store/actions/actions";
import $ from "jquery";
import "../assets/cart.css";


class Cart extends React.Component {

  payOperation = (e) => {
    if (this.props.total === 0) {
      e.preventDefault();
      $("#err-txt").fadeIn();
    } else {
      this.props.clearPayCart();
      alert("Din beställning är godkänt");
      this.props.history.push("/")
    }
  };

  render() {
    return (
      <div>
        <div id="title-search-section" style={{justifyContent: "center"}}>
          <h2 id="datorTitel">Din Varukorg</h2>
        </div>

        <div className="cart-main-container">
          {this.props.cartItems.map((item, index) => {
              return (
                <div key={index} className="cart-item-box">
                  <img src={item.product.pic} alt="Bild saknas"></img>
                  <div className="cart-item-box-title">
                    <h4>{item.product.name}</h4>
                    <table id="cart-details-table">
                      <thead>
                        <tr>
                          <td><b>Pris</b></td>
                          <td><b>Antal</b></td>
                          <td><b>Total</b></td>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>{item.product.price} kr</td>
                          <td>{item.quantity} st.</td>
                          <td>{parseInt(item.product.price) * item.quantity} kr</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  
                  <div className="cart-item-box-btn">
                    <button
                      onClick={() => this.props.removeFromCart(index)}
                      className="btn btn-danger"
                    >
                      <i className="fa fa-trash"></i> <span>Ta bort</span>
                    </button>
                  </div>
                </div>
              );
            }
          )}

          <p id="err-txt" style={{ display: "none" }}>
            <i className="fa fa-exclamation-triangle"></i> Du har inga varor i
            korgen
          </p>

          <h4>
            <b>Total :</b> {this.props.total} kr
          </h4>

          <button
            className="btn btn-primary btn-block"
            onClick={this.payOperation}
          >
            Betala
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    cartItems: state.cart,
    total: state.cart.reduce(
      (total, item) => total + item.quantity * parseInt(item.product.price),
      0
    ),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    removeFromCart: (index) => dispatch(removeFromCart(index)),
    clearPayCart: () => dispatch(clearPayCart()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Cart);
