import {React, Component} from "react";
import {Link} from "react-router-dom";
import {connect} from "react-redux";
import $ from "jquery";
import "../assets/shoppingBasket.css";


class BasketItemsNumber extends Component{

  componentDidMount(){
    if (this.props.totalQuantity === 0){
      $(".spano").hide();
    }else{
      $(".spano").fadeIn();
    }
  }
  componentDidUpdate(){
    if (this.props.totalQuantity === 0){
      $(".spano").hide();
    }else{
      $(".spano").fadeIn();
    }
  }

  render(){
    return(
      <Link to="/cart">
        <div id="shopping-basket">
          <i className="fa fa-shopping-cart">
          <span className="spano">{this.props.totalQuantity}</span>
          </i>
        </div>
      </Link>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    totalQuantity: state.cart.reduce((total, item) => total + parseInt(item.quantity), 0),
  };
}

export default connect(mapStateToProps)(BasketItemsNumber);