import { React, Component } from "react";
import { connect } from "react-redux";
import GetById from "../Api/itemById";
import "../assets/unikProduct.css";
import { addToCart } from "../Store/actions/actions";

class SingleProduct extends Component {
  state = {
    loading: true,
    product: {},
    quantity: 1
  };

  componentDidMount() {
    const id = this.props.match.params.id;
    console.log({ id });

    GetById(parseInt(id)).then((product) => {
      this.setState({
        product,
        loading: false,
      });
    });
    
  }


  handleQuantity = (event) => {
    if(event.target.value < 0)
    {return;}
    else {
      this.setState({
        quantity: event.target.value
      })
    }
  }

  addToCart = (product) => {
    this.props.addToCart(product, this.state.quantity);
  }

  render() {
    const item = this.state.product;
    const itemType = () =>{
      if(item.genre === "computers"){
        return(
          <div>
            <h3>Specifikationer:</h3>
            <table>
              <tr>
                <td><b>Processor</b></td>
                <td>{item.spec.processor}</td>
              </tr>
              <tr>
                <td><b>Modellnummer</b></td>
                <td>{item.spec.Modellnummer}</td>
              </tr>
              <tr>
                <td><b>Antal kärnor (cores)</b></td>
                <td>{item.spec.numberOfCells}</td>
              </tr>
              <tr>
                <td><b>CPU Cache</b></td>
                <td>{item.spec.CPUcache}</td>
              </tr>
              <tr>
                <td><b>Internminne RAM (GB)</b></td>
                <td>{item.spec.RAMmemory}</td>
              </tr>
            </table>
          </div>
        )
      }
      else if(item.genre === "phones"){
        return (
        <div>
            <h3>Specifikationer:</h3>
            <table>
              <tr>
                <td><b>Färg</b></td>
                <td>{item.spec.color}</td>
              </tr>
              <tr>
                <td><b>Typ av operativsystem</b></td>
                <td>{item.spec.OStype}</td>
              </tr>
              <tr>
                <td><b>Processor</b></td>
                <td>{item.spec.Processor}</td>
              </tr>
              <tr>
                <td><b>Antal kameror</b></td>
                <td>{item.spec.AntalKameror}</td>
              </tr>
              <tr>
                <td><b>Bakkamera - Upplösning (megapixlar)</b></td>
                <td>{item.spec.Bakkamera}</td>
              </tr>
              <tr>
                <td><b>Frontkamera - Upplösning (megapixlar)</b></td>
                <td>{item.spec.Frontkamera}</td>
              </tr>
              <tr>
                <td><b>Display (Skärm)</b></td>
                <td>{item.spec.Display}</td>
              </tr>
              <tr>
                <td><b>Refresh rate (Hz)</b></td>
                <td>{item.spec.RefreshRate}</td>
              </tr>
              <tr>
                <td><b>Internminne RAM (GB)</b></td>
                <td>{item.spec.RAMmemory}</td>
              </tr>
              <tr>
                <td><b>Internt lagringsutrymme (GB)</b></td>
                <td>{item.spec.lagringsutrymme}</td>
              </tr>
            </table>
          </div>
        )
      }
      else if(item.genre === "surfplattor"){
        return (
        <div>
            <h3>Specifikationer:</h3>
            <table>
              <tr>
                <td><b>Färg</b></td>
                <td>{item.spec.color}</td>
              </tr>
              <tr>
                <td><b>Skärmstorlek (tum)</b></td>
                <td>{item.spec.ScreenSize}</td>
              </tr>
              <tr>
                <td><b>Typ av operativsystem</b></td>
                <td>{item.spec.OStype}</td>
              </tr>
              <tr>
                <td><b>Processor</b></td>
                <td>{item.spec.Processor}</td>
              </tr>
              <tr>
                <td><b>Display (Skärm)</b></td>
                <td>{item.spec.Display}</td>
              </tr>
              <tr>
                <td><b>Batteri (mA)</b></td>
                <td>{item.spec.Batteri}</td>
              </tr>
              <tr>
                <td><b>Internminne RAM (GB)</b></td>
                <td>{item.spec.RAMmemory} GB</td>
              </tr>
              <tr>
                <td><b>Internt lagringsutrymme (GB)</b></td>
                <td>{item.spec.lagringsutrymme} GB</td>
              </tr>
            </table>
          </div>
        )
      }
      else if(item.genre === "bild"){
        return (
        <div>
            <h3>Specifikationer:</h3>
            <table>
              <tr>
                <td><b>Modellnamn</b></td>
                <td>{item.spec.Modellnamn}</td>
              </tr>
              <tr>
                <td><b>Skärmstorlek i (tum)</b></td>
                <td>{item.spec.ScreenSize}</td>
              </tr>
              <tr>
                <td><b>Ljuskälla / skärmtyp</b></td>
                <td>{item.spec.LightSourceorScreentype}</td>
              </tr>
              <tr>
                <td><b>Upplösning (i pixlar)</b></td>
                <td>{item.spec.UpplösningPixlar}</td>
              </tr>
              <tr>
                <td><b>Smart TV</b></td>
                <td>{item.spec.smartTV}</td>
              </tr>
              <tr>
                <td><b>Aspect Ratio</b></td>
                <td>{item.spec.AspectRatio}</td>
              </tr>
              <tr>
                <td><b>Inbyggd Högtalare</b></td>
                <td>{item.spec.InbyggdHögtalare}</td>
              </tr>
            </table>
          </div>
        )
      }
      else if(item.genre === "ljud"){
        return (
        <div>
            <h3>Specifikationer:</h3>
            <table>
              <tr>
                <td><b>Modellnamn</b></td>
                <td>{item.spec.Modellnamn}</td>
              </tr>
              <tr>
                <td><b>Typ av hörlurar</b></td>
                <td>{item.spec.TypeOfBuds}</td>
              </tr>
              <tr>
                <td><b>Kompatibel med följande operativsystem</b></td>
                <td>{item.spec.compatiblrWith}</td>
              </tr>
              <tr>
                <td><b>Anslutning</b></td>
                <td>{item.spec.Anslutning}</td>
              </tr>
              <tr>
                <td><b>Driftstid (timmar)</b></td>
                <td>{item.spec.Driftstid} timmar</td>
              </tr>
            </table>
          </div>
        )
      }
    }

    if (this.state.loading) return "Loading ... ";

    const product = this.state.product;

    return (
      <div className="singleProductContainer">
        <section className="block-1">
          <div id="img_ID"><img src={this.state.product.pic} alt="Bild Saknas"></img></div>
          <div id="description-box">
            <h1>{this.state.product.name}</h1>
            <p>{this.state.product.about}</p>
            <h3>
              Pris: <b>{item.price} kr</b>
            </h3>

            <div className="add-to-basket">
              <input type="number" value={this.state.quantity} onChange={this.handleQuantity}></input>
              <button id="add-to-basket-btn" onClick={() => this.addToCart(product)}>
                <b>
                  <i className="fa fa-plus"></i> Lägg i varukorg
                </b>
              </button>
            </div>
          </div>
        </section>

        <section className="block-2" >
          {itemType()}
        </section>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    addToCart: (productInfo, quantity) => dispatch(addToCart(productInfo, quantity)),
  }
}

export default connect(null, mapDispatchToProps)(SingleProduct);
