import { React, Component } from "react";
import Database from "../Api/Database.json";
import "../assets/datorer.css";

class Datorer extends Component {
  state = {
    items: Database,
    searchValue: "",
  };

  handleSrech = (e) => {
    this.setState({
      searchValue: e.target.value,
    });
  };

  render() {
    return (
      <div>
        <div id="title-search-section">
          <h2 id="datorTitel">
            <i className="fa fa-laptop"></i> Datorer
          </h2>
          <input
            className="form-control me-2"
            type="search"
            placeholder="Search"
            aria-label="Search"
            value={this.state.searchValue}
            onChange={this.handleSrech}
          ></input>
        </div>
        <div className="datorer-container">
          {this.state.items.map((item, index) => {
            if (
              this.state.searchValue !== "" &&
              item.name
                .toLowerCase()
                .indexOf(this.state.searchValue.toLowerCase()) === -1
            ) {
              return null;
            }
            while (item.genre === "computers") {
              return (
                <div key={index}>
                  <img src={item.pic} alt="Dator"></img>
                  <h4>{item.name}</h4>
                  <b>{item.price} kr</b> <br />
                  <a href={"/products/" + item.id} id="btnInfo">
                    Visa Info
                  </a>
                </div>
              );
            }
          })}
        </div>
      </div>
    );
  }
}

export default Datorer;
