import "./App.css";
import {BrowserRouter as Router, Route, Link} from "react-router-dom";
import Home from "./pages/Home";
import Ljud from "./pages/Ljud";
import Bild from "./pages/Bild";
import Datorer from "./pages/Datorer";
import Mobiler from "./pages/Mobiler";
import Surfplatta from "./pages/Surfplatta";
import SingleProduct from "./pages/SingleProduct";
import BasketItemsNumber from "./components/numberOfItemsInBasket";
import Cart from "./pages/Cart";
import {Provider} from "react-redux";
import store from "./Store/store";
import $ from "jquery";
import Clock from "./components/clock";


function App() {


  return (
    <Router>
      <div id="container">
        <section id="my-nav-bar">
          <div id="logo-container">
            <Link to="/el-store/">
              <span id="logo">Sweria <i className="fa fa-leaf"></i><br />Electronics</span> 
            </Link>
          </div>

          <div className="navbar-middle-section">
            <ul className="my-nav-list" >
            <li>
                <Link to="/el-store/">
                  <i className="fa fa-home"></i>
                </Link>
              </li>
              <li>
                <Link to="/datorer">
                  Datorer
                </Link>
              </li>
              <li>
                <Link to="/mobiler">
                  Mobiltelefon
                </Link>
              </li>
              <li>
                <Link to="/surfplattor">
                  Surfplatta
                </Link>
              </li>
              <li>
                <Link to="/bild">
                  TV/Bild
                </Link>
              </li>
              <li>
                <Link to="/ljud">
                  Ljud
                </Link>
              </li>
            </ul>             
          </div>

          <div id="basketContainer"><BasketItemsNumber /></div>
        </section>
        <section id="my-nav-bar2">
          <span onClick={()=> {$("#responsiv-ul").slideToggle()}}><i className="fa fa-bars" ></i></span>
          <Clock />
          <div id="responsiv_basket"><BasketItemsNumber /></div>
        </section>
        <ul id="responsiv-ul">
              <li>
                <Link to="/el-store/" onClick={()=> {$("#responsiv-ul").slideToggle()}}>
                  <i className="fa fa-home" ></i>
                </Link>
              </li>
              <li onClick={()=> {$("#responsiv-ul").slideToggle()}}>
                <Link to="/datorer">
                  Datorer
                </Link>
              </li>
              <li onClick={()=> {$("#responsiv-ul").slideToggle()}}>
                <Link to="/mobiler">
                  Mobiltelefon
                </Link>
              </li>
              <li onClick={()=> {$("#responsiv-ul").slideToggle()}}>
                <Link to="/surfplattor">
                  Surfplatta
                </Link>
              </li >
              <li onClick={()=> {$("#responsiv-ul").slideToggle()}}>
                <Link to="/bild">
                  TV/Bild
                </Link>
              </li>
              <li onClick={()=> {$("#responsiv-ul").slideToggle()}}>
                <Link to="/ljud">
                  Ljud
                </Link>
              </li>
            </ul>

        <main>
          <Route path="/el-store/" component={Home} exact ></Route>
          <Route path="/surfplattor" component={Surfplatta} exact></Route>
          <Route path="/datorer" component={Datorer} exact></Route>
          <Route path="/ljud" component={Ljud} exact></Route>
          <Route path="/bild" component={Bild} exact></Route>
          <Route path="/mobiler" component={Mobiler} exact></Route>
          <Route path="/products/:id" component={SingleProduct} exact></Route>
          <Route path="/cart" component={Cart} exact></Route>
        </main>
        
      </div>
    </Router>
  );
}

function AppwithStore() {
  return <Provider store={store}>
    <App />
  </Provider>
}

export default AppwithStore;
