import {React, Component} from "react";
import getAll from "../Api/artiklar";
import "../assets/datorer.css";

class Home extends Component{

  state={
    items: [],
    searchValue: ""
  }

  componentDidMount(){
    getAll().then( data => {
      this.setState({items: data})
    });
  }

  handleSrech = (e) =>{
    this.setState({
      searchValue: e.target.value
    })
  }

  render(){
    return(
      <div id="main_template">
        <div id="title-search-section">
          <h2 id="datorTitel"><i className="fa fa-microchip"></i> Alla produkter</h2>
          <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" value={this.state.searchValue} onChange={this.handleSrech}></input>
        </div>
        
        <div className="datorer-container">
          {
          this.state.items.map((item, index) =>
            {
              if (
                this.state.searchValue !== "" && 
                item.name.toLowerCase().indexOf(this.state.searchValue.toLowerCase()) === -1
              ) {
                return null;
              }
              return <div key={index}>
                  <img src={item.pic} alt="Bild saknas"></img>
                  <h4>{item.name}</h4>
                  <b>{item.price} kr</b>
                  <a href={"/products/" + item.id} id="btnInfo">Visa Info</a>
              </div>
            })
          }
        </div>
      </div>
    )
  }
}

export default Home; 