import { React, Component } from "react";

class Clock extends Component{
  state = {
    time : ""
  }

  componentDidMount(){
    setInterval(() => {
      this.setState({
        time: new Date().toLocaleTimeString()
      });
    }, 800);
  }

  render(){
    return (
      <div style={{color:"white"}}>
        {this.state.time}
      </div>
    )
  }
}

export default Clock;